import axios from 'axios';

class AuthService {
  constructor() {
    let service = axios.create({
      baseURL: process.env.REACT_APP_AUTHSERVICE_BASE_URL,
      withCredentials: true
    });
    this.service = service;
  }

  signup = (username, password, personalEmail, fullName) => {
    return this.service.post('/signup', {username:username, password:password, personalEmail:personalEmail, fullName:fullName})
    .then(response => response.data)
  }

  login = (username, password) =>{
    return this.service.post('/login', {username, password})
    .then(response => response.data)
  }

  currentUser = () =>{
    return this.service.get('/getcurrentuser')
    .then(response => response.data)
  }

  logout = () =>{
    return this.service.post('/logout', {})
    .then(response => response.data)
  }
}

export default AuthService;